
import { TiLocale } from '@cloud/tiny3';
import { currencyUSD, hken_US, hkes_US, hkpt_BR, hkzh_CN } from '@cloud/tinyplus3/locale/i18n';
import { siteConfig } from './siteConfig';

export class TpConfig {
    public static init(language: string = 'zh-CN'): void {
        const words: any = TiLocale.getWords();
        // 设置所有语言包
        TiLocale.setWords({
            'zh-CN': { ...words['zh-CN'], ...hkzh_CN, ...currencyUSD, ...siteConfig },
            'en-US': { ...words['en-US'], ...hken_US, ...currencyUSD, ...siteConfig },
            'es-US': { ...words['es-US'], ...hkes_US, ...currencyUSD, ...siteConfig },
            'pt-BR': { ...words['pt-BR'], ...hkpt_BR, ...currencyUSD, ...siteConfig }
        });
        // 设置默认语言
        TiLocale.setLocale(language);
    }
}
